import React, { useState, useEffect } from 'react';
import axios from 'axios';
import DrawerComponent from '../layouts/GenericDrawer';

const withEntityManagement = (WrappedComponent, FormComponent, entityName) => {
    return function EntityManagement(props) {
        const [selectedEntity, setSelectedEntity] = useState(null);
        const [open, setOpen] = useState(false);
        const [mode, setMode] = useState('view');


        const handleView = async (entity) => {
            try {
                console.log('view clicked for entity:', entity)
                const response = await axios.get(`http://localhost:5001/api/${entityName}/${entity._id}`);
                setSelectedEntity(response.data);
                setMode('view');
                setOpen(true);
            } catch (error) {
                console.error(`Failed to fetch ${entityName} details:`, error);
            }
        };

        const handleEdit = async (entity) => {
            try {
                const response = await axios.get(`http://localhost:5001/api/${entityName}/${entity._id}`);
                setSelectedEntity(response.data);
                setMode('edit');
                setOpen(true);
            } catch (error) {
                console.error(`Failed to fetch ${entityName} details for editing:`, error);
            }
        };

        const handleAddNew = () => {
            setSelectedEntity(null);
            setMode('add');
            setOpen(true);
        };

        const handleClose = () => {
            setOpen(false);
            setSelectedEntity(null);
            setMode('view');
        };

        const handleSave = async (entityData) => {
            try {
                if (mode === 'add') {
                    await axios.post(`http://localhost:5001/api/${entityName}`, entityData);
                } else if (mode === 'edit') {
                    await axios.put(`http://localhost:5001/api/${entityName}/${selectedEntity._id}`, entityData);
                }
                handleClose();
            } catch (error) {
                console.error(`Failed to save ${entityName}:`, error);
            }
        };

        return (
            <>
                <WrappedComponent
                    {...props}
                    handleView={handleView}
                    handleEdit={handleEdit}
                    handleAddNew={handleAddNew}
                />
                <DrawerComponent open={open} onClose={handleClose}>
                    <FormComponent
                        mode={mode}
                        onClose={handleClose}
                        onSave={handleSave}
                        entityData={selectedEntity}
                    />
                </DrawerComponent>
            </>
        );
    };
};

export default withEntityManagement;